<template>
	<div class="main-container">
    <div class="input-field">
      <input v-model="username" :class="{ 'text-green': isGood.username }" id="username" type="text" class="validate">
      <label for="username">Username</label>
    </div>
    <div class="input-field">
      <input v-model="password" :class="{ green: isGood.password }" id="password" type="password" class="validate">
      <label for="password">Password</label>
    </div>
    <div class="input-field">
      <input v-model="password2" :class="{ green: isGood.password2 }" id="password2" type="password" class="validate">
      <label for="password2">Password once again</label>
    </div>

    <div @click="onSignUp" class="sign-up-btn">Sign up</div>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        username: null,
        password: null,
        password2: null,
        isGood: {
          username: false,
          password: false,
          password2: false
        }
      }
    },
    watch: {
      username: function(val) {
        let regExp = /^[a-zA-Z0-9]{6,}$/
        this.isGood.username = regExp.test(val) ? 1 : 0 
      },
      password: function(val) {
        let regExp = /^(?=.*[0-9]+.*)(?=.*[a-zA-Z]+.*)[0-9a-zA-Z]{6,}$/
        this.isGood.password = regExp.test(val) ? 1 : 0
        this.isGood.password2 = this.isGood.password && val == this.password2 ? 1 : 0
      },
      password2: function(val) {
        this.isGood.password2 = this.isGood.password && this.password == val ? 1 : 0
      }
    },
    methods: {
      onSignUp() {
        if (this.username.length > 5) {
          alert()
        }
      }
    }
  }
</script>

<style lang="sass">
  input
    border-bottom-width: 0
  
  .sign-up-btn
    position: relative
    left: 50%
    margin-top: 25px
    transform: translateX(-50%)
    width: 140px
    height: 40px
    margin-bottom: 15px
    background-color: lightblue
    text-decoration: none
    text-align: center
    line-height: 40px
    border-radius: 20px

  .text-green
    color: green
</style>